.home {
    padding: 20px;
  }
  
  /* Estilos para la lista de videojuegos */
  .videogamesContainer {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
  }
  
  /* Estilos para cada tarjeta de videojuego */
  .videogameCard {
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 10px;
    background-color: #f9f9f9;
  }
  
  /* Estilos para la imagen del videojuego */
  .videogameImage {
    width: 100%;
    height: auto;
    border-radius: 8px;
  }
  
  /* Estilos para el nombre del videojuego */
  .videogameName {
    margin-top: 10px;
    font-size: 18px;
  }
  
  /* Estilos para los g√©neros del videojuego */
  .videogameGenres {
    margin-top: 5px;
    font-size: 14px;
    color: #666;
  }